import React, { useEffect, useState } from "react";
import axios from "axios";
import styled from "styled-components";
import { ModalButton } from "./Main";
import { useNavigate } from "react-router-dom";
import { Line } from "react-chartjs-2";
import Select from "react-select";
import {
  registerables,
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  ArcElement,
  CategoryScale,
} from "chart.js";

ChartJS.register(
  Title,
  Tooltip,
  Legend,
  ArcElement,
  CategoryScale,
  ...registerables
);

const DivDashboard = styled.div`
  width: 80vw;
  height: 80vh;
  background-color: white;
`;

const DivGraph = styled.div`
  width: 70vw;
  height: 80vh;
`;

const DivOption = styled.div`
  display: flex;
  flex-direction: row;

`

const API_URL = process.env.REACT_APP_URL;
const API_KEY = process.env.REACT_APP_AWS_API_KEY;


const Dashboard = () => {
  const navigate = useNavigate();
  const currentYear = new Date().getFullYear().toString();
  const currentMonth = (new Date().getMonth() + 1).toString().padStart(2, "0");

  const [data, setData] = useState(null);
  const [month, setMonth] = useState(currentMonth); //    
  const [year, setYear] = useState(currentYear); //    

  //      
  const filteredData = data?.filter((item) => {
    const itemDate = new Date(item.date);
    return (
      itemDate.getFullYear() === parseInt(year) &&
      (itemDate.getMonth() + 1).toString().padStart(2, "0") === month
    );
  }).sort((a, b) => new Date(a.date) - new Date(b.date)); //   

  //   
  const chartData = {
    labels: filteredData?.map((item) => item.date.split("-")[2]), // 
    datasets: [
      {
        label: `${year}-${month}`,
        data: filteredData?.map((item) => item.number),
        fill: false,
        borderColor: "rgb(75, 192, 192)",
        tension: 0.1,
      },
    ],
  };

  //   
  const chartOptions = {
    scales: {
      y: {
        beginAtZero: true,
      },
    },
  };

  //  
  const fetchData = async () => {
    try {
      const response =  await axios.get(
        `${API_URL}/data?year=${year}&month=${month}`,
        {
          headers: {
            'x-api-key': API_KEY
          }
        }
      );
      setData(response.data);
      console.log("Data fetched successfully:", response.data);
    } catch (error) {
      console.error("Error fetching data:", error);
    }
  };

  //      
  useEffect(() => {
    fetchData();
  }, [year, month]); // year month     

  //   
  const monthOptions = [
    { value: "01", label: "1" },
    { value: "02", label: "2" },
    { value: "03", label: "3" },
    { value: "04", label: "4" },
    { value: "05", label: "5" },
    { value: "06", label: "6" },
    { value: "07", label: "7" },
    { value: "08", label: "8" },
    { value: "09", label: "9" },
    { value: "10", label: "10" },
    { value: "11", label: "11" },
    { value: "12", label: "12" },
  ];

  //   
  const yearOptions = [
    { value: "2024", label: "2024" },
    { value: "2025", label: "2025" },
  ];

  return (
    <div
      style={{
        height: "100vh",
        width: "100vw",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <DivDashboard className="Modal">
        <DivOption>
          {/*     */}
          <Select
            options={yearOptions}
            value={yearOptions.find((option) => option.value === year)}
            onChange={(selectedOption) => setYear(selectedOption.value)}
          />
          {/*     */}
          <Select
            options={monthOptions}
            value={monthOptions.find((option) => option.value === month)}
            onChange={(selectedOption) => setMonth(selectedOption.value)}
          />
        </DivOption>
        {/*   */}
        <DivGraph>
          <Line data={chartData} options={chartOptions} />
        </DivGraph>
        {/*    */}
        <ModalButton onClick={() => navigate("/")}> </ModalButton>
      </DivDashboard>
    </div>
  );
};

export default Dashboard;